.singlePost2{
    float: left;
}
.singlePostImg{
    width: 65%;
    height: 20%;
}
.singlePostWrapper{
    padding: 20px;
    padding-right: 0;
    border-radius: 5px;
    object-fit: cover;
}
.singlePostTitle{
    text-align: center;
    font-family: 'Lora',serif;
    font-size: 28px; 
}
.singlePostEdit{
    float: right;
    font-size: 16px;
}
.singlePostIcon{
    margin-left: 10px;
}
.singlePostIcon:first-child{
    color: teal;
}
.singlePostIcon:last-child{
    color: tomato;
}
.singlePostInfo{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    font-family: 'Varela Round',sans-serif;
    color: #b39656; 
}
.singlePostDesc{
    color: #666;
    font-size: 18px;
    line-height: 35px;
}
.singlePostDesc::first-letter{
    margin-left: 20px;
    font-size: 30px;
    font-weight: 600;
}
.i2{
    height: 700px;
    width: 700px;
    margin-left: 50px;
}